import React, { Component } from 'react';
import './layout.css';
const ScrollMagic = window.ScrollMagic;
const controller = window.controller;
const FuLiUrl = window.FuLiUrl;
const BaoMingUrl = window.BaoMingUrl;
const CanYuUrl = window.CanYuUrl;
class App extends Component {

    constructor(props) {
        super(props);
        this.state = {
            showShareMask: false,
            isPlay: false,
            img1: require('./imgs/1.jpg'),
            img2: require('./imgs/2.jpg'),
            img3: require('./imgs/3.jpg'),
            img4: require('./imgs/4.jpg'),
            img5: require('./imgs/5.jpg'),
            img6: require('./imgs/6.jpg'),
            img7: require('./imgs/7.jpg'),
            share: require('./imgs/s-1.gif'),
            video: require('./imgs/movie.mp4'),
            poster:require('./imgs/poster.png')
        }
    }

    componentWillMount() {
        console.log(ScrollMagic.Scene);
    }

    componentDidMount() {

        var scene1 = new ScrollMagic.Scene({ triggerElement: "#t1" })
            .setClassToggle("#s3-1", "anis21")
            // .addIndicators({ name: "1 - add a class" }) // add indicators (requires plugin)
            .addTo(controller);

        var scene2 = new ScrollMagic.Scene({ triggerElement: "#t2" })
            .setClassToggle("#s3-2", "anis21")
            // .addIndicators({ name: "2 - add a class" }) // add indicators (requires plugin)
            .addTo(controller);

        var scene3 = new ScrollMagic.Scene({ triggerElement: "#t3" })
            .setClassToggle("#s3-3", "anis21")
            // .addIndicators({ name: "3 - add a class" }) // add indicators (requires plugin)
            .addTo(controller);


        var scene4 = new ScrollMagic.Scene({ triggerElement: "#t4" })
            .setClassToggle("#s5-1", "animoveinleft")
            // .addIndicators({ name: "3 - add a class" }) // add indicators (requires plugin)
            .addTo(controller);

        var scene5 = new ScrollMagic.Scene({ triggerElement: "#t5" })
            .setClassToggle("#s5-2", "animoveinright")
            // .addIndicators({ name: "3 - add a class" }) // add indicators (requires plugin)
            .addTo(controller);

        var scene6 = new ScrollMagic.Scene({ triggerElement: "#s5-2" })
            .setClassToggle("#s5-3", "animoveinleft")
            // .addIndicators({ name: "3 - add a class" }) // add indicators (requires plugin)
            .addTo(controller);

        var scene7 = new ScrollMagic.Scene({ triggerElement: "#s5-3" })
            .setClassToggle("#s5-4", "animoveinright")
            // .addIndicators({ name: "3 - add a class" }) // add indicators (requires plugin)
            .addTo(controller);

        var scene8 = new ScrollMagic.Scene({ triggerElement: "#t6" })
            .setClassToggle("#shand", "s-hand-ani")
            // .addIndicators({ name: "3 - add a class" }) // add indicators (requires plugin)
            .addTo(controller);

        var scene8 = new ScrollMagic.Scene({ triggerElement: "#t6" })
            .setClassToggle("#sline", "s-line-ani")
            // .addIndicators({ name: "3 - add a class" }) // add indicators (requires plugin)
            .addTo(controller);

        /* 移除调试 */
        // scene1.removeIndicators();
        // scene2.removeIndicators();
        // scene3.removeIndicators();
        // scene4.removeIndicators();
        // scene5.removeIndicators();
        // scene6.removeIndicators();
        // scene7.removeIndicators();

    }


    componentDidUpdate(a, b) {

    }

    /* 领取福利 */
    LingQuFuLi() {
        window.location.href = FuLiUrl;
    }

    /* 报名 */
    BaoMing() {
        window.location.href = BaoMingUrl;
    }

    /* 立即参与 */
    LiJiCanYu() {
        window.location.href = CanYuUrl;
    }

    /* 显示分享遮罩层 */
    TiggerShareMask() {
        this.setState({
            showShareMask: !this.state.showShareMask
        });
    }

    /* 视频播放暂停 */
    TriggerVideo() {
        let video = this.refs.video;
        let flag = this.state.isPlay;
        this.setState({
            isPlay: !this.state.isPlay
        });


        if (flag) {
            video.pause();
        } else {
            video.play();
        }

    }

    render() {
        /* 分享遮罩层 */
        const RenderShareMask = <div onClick={this.TiggerShareMask.bind(this)} className='page-share'>
            <div className='share-img'>
                <img src={this.state.share} />
            </div>
        </div>;

        /* 音频播放按钮 */
        const RenderPlayBtn = <div onClick={this.TriggerVideo.bind(this)} className="s2-video-btn"></div>;

        return (
            <div className="main">
                {/* 分享遮罩层 */}
                {this.state.showShareMask && RenderShareMask}
                {/* 内容 */}
                <div className="section">
                    <div className="s-star"></div>
                    <img src={this.state.img1} />
                </div>
                <div className="section">
                    {/* 播放的视频 */}
                    <div className="s2-video">
                        {!this.state.isPlay && RenderPlayBtn}
                        <video poster={this.state.poster} x-webkit-airplay="true" x5-playsinline="true" webkit-playsinline="true" playsinline="true" preload="auto" onClick={this.TriggerVideo.bind(this)} ref="video" className='s2-video-stage'>
                            <source src={this.state.video} type="video/mp4" />
                        </video>

                    </div>
                    <img src={this.state.img2} />
                    <div id="t1" className="m-trigger s3trigger1" ></div>
                    <div id="t2" className="m-trigger s3trigger2" ></div>
                    <div id="t3" className="m-trigger s3trigger3" ></div>
                </div>
                <div id="s3" className="section">
                    <div className="s3-inner">
                        <div id="s3-1" className="s2-item s2-1 animated"></div>
                        <div id="s3-2" className="s2-item s2-2 animated"></div>
                        <div id="s3-3" className="s2-item s2-3 animated"></div>
                    </div>
                    <div id="t6" className="m-trigger s3trigger3" ></div>
                    <img src={this.state.img3} />
                </div>
                <div className="section">
                    {/* 菜单动画 */}
                    <div className="s-menu"></div>
                    <div className="s-line-mask">
                        <div id="sline" className="s-line"></div>
                    </div>                    
                    <div id='shand' className="s-hand s-hand-ani"></div>
                    <img src={this.state.img4} />
                    <div id="t4" className="m-trigger s3trigger2"></div>
                    <div id="t5" className="m-trigger s3trigger5"></div>
                    {/* 立即参与 */}
                    <div onClick={this.LiJiCanYu.bind(this)} className="s4-btn"></div>
                </div>
                <div className="section">
                    <div id="s5-1" className="s5-item s5-1 animated"></div>
                    <div id="s5-2" className="s5-item s5-2 animated"></div>
                    <div id="s5-3" className="s5-item s5-3 animated"></div>
                    <div id="s5-4" className="s5-item s5-4 animated"></div>

                    <img src={this.state.img5} />
                </div>
                <div className="section">
                    <div onClick={this.LingQuFuLi.bind(this)} className="m-trigger s6-btn"></div>
                    <img src={this.state.img6} />
                </div>
                <div className="section">
                    <img src={this.state.img7} />
                </div>
                {/* 底部按钮 */}
                <div className="section s-footer">
                    <div onClick={this.BaoMing.bind(this)} className="s-btn s-btn-1"></div>
                    <div onClick={this.TiggerShareMask.bind(this)} className="s-btn s-btn-2"></div>
                </div>
            </div>
        );
    }
}

export default App;



